@import url(https://fonts.googleapis.com/css2?family=Poppins&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.navbar{
    top: 5%;
    font-size: 17px;
    width: 80%;
    left: 10%;
    padding: 30px;
}
.navbar-brand{
    font-size: 2rem !important;
}
.text-nav{
    color: #F1FAEE !important;
}
footer{
    background-color: #F1FAEE;
    padding: 30px 20px;
}
.foot_container{
    width: 80%;
    margin: auto;
    padding-top: 30px;
}
.col_footer{
    margin-left: 200px;
}
.li_tittle{
    border-bottom:1px solid #1D3557;
    display: inline-block;
    margin-bottom: 10px;
}
.li_tittle > a{
    color: #1D3557;
    text-decoration: none;
}
.list > li > a{
    color: #1D3557;
    text-decoration: none;
}
.contact{
    color: #1D3557;
    text-decoration: none;
    font-style: normal;
}
.icons{
    margin-top: 20px;
}
.icons > li > a{
    color: #1D3557;
}
.main_page {
    text-align: center;
    background-image: linear-gradient(rgba(29, 53, 87, 0.4), rgba(29, 53, 87, 0.2)),
        url(https://ik.imagekit.io/4s0yrabzrtp/banner_v9N3t_CYn.png?ik-sdk-version=javascript-1.4.3&updatedAt=1648078767123);
    height: 100vh;
    background-position: center;
    background-size: cover;
    position: relative;
}

.tittle_main{
    top: 33%;
    position: relative;
    color: aliceblue;
    font-size: 4rem;
    font-weight: 600;
}

.page {
    text-align: center;
    background-image: linear-gradient(rgba(223, 125, 46, 0.4), rgba(0, 0, 0, 0.322)),
        url(https://ik.imagekit.io/4s0yrabzrtp/DSC_6189-HDR_jkgN3HYG2.png?ik-sdk-version=javascript-1.4.3&updatedAt=1661617096690);
    height: 80vh;
    background-position: center;
    background-size: cover;
    position: relative;
}

.cont{
    background-color: rgb(19, 9, 1);
    color: aliceblue;
}

.tagcloud {
    font-size: 16px;
}

.tagcloud--item {
    padding: 2px 4px;
    background-color: transparent;
    border: 1px solid transparent;
    cursor: pointer;
}

.tagcloud--item:hover {
    background-color: rgba(0, 0, 0, .1);
    border: 1px solid #333;
    border-radius: 2px;
    opacity: 1 !important;
    z-index: 100 !important;
}

.light .tagcloud--item {
    color: #fff;
}

.light .tagcloud--item:hover {
    background-color: rgba(255, 255, 255, .1);
    border: 1px solid #fff;
}
